* ::-webkit-scrollbar {
  width: 4px;
}

::-webkit-scrollbar-track {
  background: #141b2d;
}

::-webkit-scrollbar-thumb {
  background: #141b2d;
}

html {
  min-height: 100%;
}
body {
  color: #888ea8;
  height: 100%;
  font-size: 0.875rem;
  background: #141b2d;
  overflow-x: hidden;
  overflow-y: auto;
  letter-spacing: 0.0312rem;
  font-family: "Nunito", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #e0e6ed;
}
:focus {
  outline: none;
}
::-moz-selection {
  /* Code for Firefox */
  color: #4361ee;
  background: transparent;
}
::selection {
  color: #4361ee;
  background: transparent;
}
p {
  margin-top: 0;
  margin-bottom: 0.625rem;
  color: #ebedf2;
}
hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border-top: 1px solid #191e3a;
}
strong {
  font-weight: 600;
}
code {
  color: #e7515a;
}

/*Page title*/

.page-header {
  border: 0;
  margin: 0;
}
.page-header:before,
.page-header:after {
  display: table;
  content: "";
  line-height: 0;
}
.page-header:after {
  clear: both;
}
.page-title h3 {
  margin: 0;
  margin-bottom: 0;
  font-size: 15px;
  color: #bfc9d4;
  font-weight: 700;
}
.page-title span {
  display: block;
  font-size: 11px;
  color: #888;
  font-weight: normal;
}
.main-container {
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;

  -ms-flex-wrap: wrap;
  flex-wrap: wrap;

  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  padding: 0 0 0 16px;
  background-color: #141b2d !important;
}
#container.fixed-header {
  margin-top: 56px;
}
#content {
  position: relative;
  width: 50%;
  flex-grow: 8;
  margin-top: 106px;
  margin-bottom: 0;
  margin-left: 212px;
  transition: 0.6s;
}
.main-container-fluid > .main-content > .container {
  float: left;
  width: 100%;
}
#content > .wrapper {
  -webkit-transition: margin ease-in-out 0.1s;
  -moz-transition: margin ease-in-out 0.1s;
  -o-transition: margin ease-in-out 0.1s;
  transition: margin ease-in-out 0.1s;
  position: relative;
}

.widget {
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
  box-shadow: 0 0.75rem 1.5rem rgba(18, 38, 63, 0.03);
}
.layout-top-spacing {
  margin-top: 20px;
}
.layout-spacing {
  padding-bottom: 40px;
}
.layout-px-spacing {
  padding: 0 20px 0 18px !important;
  min-height: calc(100vh - 170px) !important;
}
.sidebar-closed .layout-px-spacing {
  padding: 0 16px !important;
}
.widget.box .widget-header {
  background: #0e1726;
  padding: 0px 8px 0px;
  border-top-right-radius: 6px;
  border-top-left-radius: 6px;
}
.row [class*="col-"] .widget .widget-header h4 {
  color: #ebedf2;
  font-size: 17px;
  font-weight: 600;
  margin: 0;
  padding: 16px 15px;
}
.seperator-header {
  background: transparent;
  box-shadow: none;
  margin-bottom: 40px;
  border-radius: 0;
}
.seperator-header h4 {
  margin-bottom: 0;
  line-height: 1.4;
  padding: 5px 8px;
  font-size: 15px;
  border-radius: 4px;
  letter-spacing: 1px;
  display: inline-block;
  background: rgba(0, 150, 136, 0.26);
  color: #009688;
  font-weight: 500;
}
.widget .widget-header {
  border-bottom: 0px solid #f1f2f3;
}
.widget .widget-header:before,
.widget .widget-header:after {
  display: table;
  content: "";
  line-height: 0;
}
.widget .widget-header:after {
  clear: both;
}
.widget-content-area {
  padding: 20px;
  position: relative;
  background-color: #1f2940;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
.content-area {
  max-width: 58.333333%;
  margin-left: 80px;
}

