/*
    Skills
*/

.skill {
  background-color: #1f2940;
  border-radius: 6px;
}
.skill .input-form {
  margin-bottom: 24px;
  display: flex;
  width: 50%;
  margin-right: auto;
  margin-left: auto;
}
.skill input[type="text"] {
  margin-right: 10px;
}
.skill #add-skills {
}
.skill .info {
  padding: 20px;
}
.skill .skill-name {
  font-size: 13px;
  font-weight: 700;
  margin-bottom: 0;
  margin-top: 0;
  color: #888ea8;
}

.skill .save-info {
  padding: 20px;
}
.skill .info label {
  text-transform: uppercase;
  color: #eaf1ff;
}
.skill .custom-progress.progress-up .range-count {
  margin-top: 11px;
}
.skill .custom-progress.progress-down .range-count {
  margin-top: 15px;
}
.skill .range-count {
  font-weight: 700;
  color: #3b3f5c;
}
.skill .range-count .range-count-number {
  display: inline-block;
  background: #1b2e4b;
  padding: 3px 8px;
  border-radius: 5px;
  color: #009688;
  border: 1px solid #1b2e4b;
}
.skill .range-count .range-count-unit {
  color: #009688;
}
.skill .custom-progress.top-right .range-count {
  text-align: right;
}
.skill .custom-progress.bottom-right .range-count {
  text-align: right;
}
.skill .progress-range-counter::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  background: #009688;
  cursor: pointer;
  height: 16px;
  width: 16px;
  margin-top: -4px;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}
.skill .progress-range-counter:active::-webkit-slider-thumb {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.skill .progress-range-counter:focus::-webkit-slider-thumb {
  background: #009688;
  cursor: pointer;
  height: 16px;
  width: 16px;
  margin-top: -4px;
  box-shadow: none;
}
.skill .progress-range-counter::-moz-range-thumb {
  background: #009688;
  cursor: pointer;
  height: 16px;
  width: 16px;
  margin-top: -4px;
}
input[type="range"]::-webkit-slider-runnable-track {
  background: #191e3a;
}
input[type="range"]::-moz-range-track {
  background: #191e3a;
}
input[type="range"]::-ms-track {
  background: #191e3a;
}

